import React, { useState, useContext } from 'react';
import dayjs from 'dayjs';

import ListContext from '../context';
import { Button, Col, FloatingLabel, Form, Row } from 'react-bootstrap';

const AddForm = () => {
    const [addTitle, setAddTitle] = useState("");
    const [addDescr, setAddDescr] = useState("");
    const {announcements} = useContext(ListContext);

    const submit = () => {
        const tempList = [...announcements.list];
        const addId = tempList[tempList.length - 1].id + 1;
        let item = {
            id: addId,
            title: addTitle,
            descr: addDescr,
            date: dayjs().format('YYYY.MM.DD')
        }
        
        tempList.push(item);
        announcements.setList(tempList);
        console.log(announcements.list)
    }

    const handleTitle = (event) => {
        const target = event.target;
        const value = target.value;
        setAddTitle(value);
    }

    const handleDescr = (event) => {
        const target = event.target;
        const value = target.value;
        setAddDescr(value);
    }

    return (
        <Form className="fields">
            <Row className="justify-content-center">
                <Col md={6}>
                    <FloatingLabel label="Announcement title">
                        <Form.Control
                            placeholder="Title"
                            onClick={handleTitle}
                            onChange={handleTitle}
                        />
                    </FloatingLabel>
                </Col>
            </Row>
            <Row className="justify-content-center">
                <Col md={6}>
                    <FloatingLabel label="Announcement description">
                        <Form.Control
                            as="textarea"
                            rows={3}
                            placeholder="Description"
                            onClick={handleDescr}
                            onChange={handleDescr}
                        />
                    </FloatingLabel>
                    <Button variant="success" onClick={() => submit()}>Add Announcement</Button>
                </Col>
            </Row>
        </Form>
    )
}

export default AddForm;